<template>
	<div>
		<h3>Warehouses</h3>
		<b-alert variant="success" show>
			Create and modify warehouses
		</b-alert>
		<b-row>
			<b-col md="2">
				<p>Warehouse Name</p>
				<b-form-input
					type="text"
					v-model="searchForm.name"/>
			</b-col>
			<b-col md="2">
				<p>Country</p>
				<b-form-select v-model="searchForm.country">
					<b-form-select-option :value="-1">All</b-form-select-option>
					<b-form-select-option v-for="(country, i) in countries" :key="i" :value="country._id">{{ country.name }}</b-form-select-option>
				</b-form-select>
			</b-col>
			<b-col md="2">
				<b-button
					variant="primary"
					class="mt-4"
					@click="fetch">
					Search
				</b-button>
			</b-col>
			<b-col md="6" class="text-right">
				<b-button variant="success" @click="add" class="mt-4">
					<b-icon icon="plus" scale="1.5"></b-icon> Add
				</b-button>
			</b-col>
		</b-row>
		<b-table
			:fields='fields'
			:items='list'
			:busy="loading"
			outlined
			class="mt-3"
			responsive>
			<template #cell(index)="data">
				<p>{{ (searchForm.page - 1) * searchForm.perPage + data.index + 1 }}</p>
			</template>
			<template #cell(name)="data">
				<p>{{ data.value }}</p>
			</template>
			<template #cell(country)="data">
				<p>{{ data.item.country && data.item.country.name }}</p>
			</template>
			<template #cell(edit)="data">
				<b-button variant="success" size="sm" @click="edit(data.index)" class="mr-2">
					<b-icon icon="pencil"></b-icon> Edit
				</b-button>
				<b-button variant="success" size="sm" @click="$router.push('/home/warehouse/' + data.item._id)" class="mr-2">
					<b-icon icon="clock"></b-icon> Manage Slots
				</b-button>
				<b-button variant="danger" size="sm" @click="deleteById(data.index)" class="mr-2">
					<b-icon icon="trash"></b-icon> Delete
				</b-button>
			</template>
		</b-table>
		<b-pagination
			v-model="searchForm.page"
			:total-rows="total"
			:per-page="searchForm.perPage"
			align="right">
		</b-pagination>

		<b-modal 
			v-model="addModalShow"
			title="Add Warehouse"
			hide-header-close 
			@ok="doAdd"
		>
			<b-row>
				<b-col md="4">Warehouse Name</b-col>
				<b-col md="8">
					<b-form-input
						type="text"
						v-model="warehouse.name"
						:state="!$v.warehouse.name.required ? false : null"/>
					<b-form-invalid-feedback v-if="!$v.warehouse.name.required">
						Name is required.
					</b-form-invalid-feedback>
				</b-col>
			</b-row>
			<b-row class="mt-2">
				<b-col md="4">Country</b-col>
				<b-col md="8">
					<b-form-select v-model="warehouse.country">
						<b-form-select-option v-for="(country, i) in countries" :key="i" :value="country._id">{{ country.name }}</b-form-select-option>
					</b-form-select>
				</b-col>
			</b-row>
		</b-modal>

		<b-modal 
			v-model="editModalShow"
			title="Edit Warehouse"
			hide-header-close 
			@ok="doEdit"
		>
			<b-row>
				<b-col md="4">Warehouse Name</b-col>
				<b-col md="8">
					<b-form-input
						type="text"
						v-model="warehouse.name"
						:state="!$v.warehouse.name.required ? false : null"/>
					<b-form-invalid-feedback v-if="!$v.warehouse.name.required">
						Name is required.
					</b-form-invalid-feedback>
				</b-col>
			</b-row>
			<b-row class="mt-2">
				<b-col md="4">Country</b-col>
				<b-col md="8">
					<b-form-select v-model="warehouse.country">
						<b-form-select-option v-for="(country, i) in countries" :key="i" :value="country._id">{{ country.name }}</b-form-select-option>
					</b-form-select>
				</b-col>
			</b-row>
		</b-modal>

		<b-modal 
			v-model="deleteModalShow"
			title="Delete Warehouse"
			hide-header-close 
			@ok="doDelete"
		>
			Are you sure you want to delete?
		</b-modal>
	</div>
</template>
<script>
	import { mapState } from 'vuex'
	import { required } from 'vuelidate/lib/validators'
	export default {
		name: 'WarehouseView',
		mounted() {
			this.fetch()
		},
		data() {
			return {
				fields: [
					{ key: 'index', label: 'No' },
					{ key: 'name', label: 'Warehouse Name' },
					{ key: 'country', label: 'Country' },
					{ key: 'edit', label: '-'}
				],
				loading: false,
				searchForm: {
					page: 1,
					perPage: 10,
					name: '',
					country: -1
				},
				addModalShow: false,
				editModalShow: false,
				deleteModalShow: false,
				warehouse: {
					_id: '',
					name: '',
					country: ''
				}
			}
		},
		validations: {
			warehouse: {
				name: { required }
			}
		},
		computed: {
			...mapState({
				list: state => state.warehouse.list,
				total: state => state.warehouse.total,
				countries: state => state.warehouse.countries
			}) 
		},
		methods: {
			fetch() {
				this.loading = true
				this.$store.dispatch('warehouse/fetch', this.searchForm).then(() => {
					this.loading = false
				})
			},
			add() {
				this.warehouse = {
					_id: '',
					name: '',
					country: (this.countries && this.countries.length ? this.countries[0]._id : '')
				}
				this.addModalShow = true
			},
			doAdd(evt) {
				evt.preventDefault()

				this.$v.warehouse.$touch()
				if (this.$v.warehouse.$error) return

				this.addModalShow = false
				this.$store.dispatch('warehouse/add', this.warehouse).then(() => {
					this.$toasted.success('Add Success')
					this.fetch()
				})
			}, 
			edit(idx) {
				this.warehouse = {
					_id: this.list[idx]._id,
					name: this.list[idx].name,
					country: (this.list[idx].country ? this.list[idx].country._id : '')
				}

				this.editModalShow = true
			},
			doEdit(evt) {
				evt.preventDefault()

				this.$v.warehouse.$touch()
				if (this.$v.warehouse.$error) return

				this.editModalShow = false
				this.$store.dispatch('warehouse/edit', this.warehouse).then(() => {
					this.$toasted.success('Edit Success')
					this.fetch()
				})
			},
			deleteById(idx) {
				this.warehouse._id = this.list[idx]._id
				this.deleteModalShow = true
			},
			doDelete() {
				this.$store.dispatch('warehouse/deleteById', this.warehouse._id).then(() => {
					this.$toasted.success('Delete Success')
					this.fetch()
				})
			}
		},
		watch: {
			'searchForm.page': function() {
				this.fetch()
			}
		}
	}
</script>